import theme from '../components/theme'

const Index = () => {

    return (
        <div style={styles.container}>
            <header style={styles.header}>
                <h1 style={styles.text}>SELab</h1>
                <p style={styles.text}> Resarch</p>
                <p style={styles.text}> Courses</p>
            </header>
            <div style={styles.contentContainer1}>
                <p>1</p>
            </div>
            <div style={styles.contentContainer2}>
                <p>2</p>
            </div>
            <div style={styles.contentContainer1}>
                <p>3</p>
            </div>
            <div style={styles.contentContainer2}>
                <p>4</p>
            </div>
        </div>
    )
}

const styles = {
    container: {
        ...theme.FONTS,
        margin: 0,
        padding: 0,
        width: '100%',
    },
    header: {
        position: '-webkit-sticky',
        position: 'sticky',
        top: 0,
        left: 0,
        width: '100%',
        height: 90,
        overflow: 'hidden',
        flex: 1,
        // justifyContent: 'center',
        // alignItems: 'center',
    },
    text: {
        webkitBackgroundClip: 'text',
        webkitTextFillColor: 'transparent',
        whitesSpace: 'nowrap',
        display: 'inline-block',
        flex: 1,
        marginLeft: 10,
    },
    contentContainer1: {
        height: '50vh',
        color: 'white',
        backgroundColor: theme.COLORS.BG1,
        margin: 0,
        overflow: 'hidden',
    },
    contentContainer2: {
        height: '50vh',
        color: 'black',
        backgroundColor: theme.COLORS.BG2,
        margin: 0,
        overflow: 'hidden',

    }
}

export default Index